import msTabs from '@/components/ms-tabs/ms-tabs.vue'
import chunleiVideo from '../../components/chunlei-video/chunlei-video.nvue'	
import goodsSwiper from "../../components/pyh-goodsSwiper/pyh-goodsSwiper.vue"
export default {
	components:{
		chunleiVideo,
		goodsSwiper,
		msTabs
	},
	data() {
		return {
			currentIndex:'0',
			imgList:[
				'../../static/images/swiper.png',
				'../../static/images/swiper2.png',
				'../../static/images/swiper3.png',
				'../../static/images/swiper.png',
				'../../static/images/swiper2.png'],
			background: ['color1', 'color2', 'color3'],
			indicatorDots: false,
			autoplay: false,
			interval: 2000,
			duration: 500,
			detail:{
				// title:"推荐",
				// subLable:"超划算",
				list:[
					{
						id:1,
						img:"../../static/images/4.png",
						name:"全部"
					},
					{
						id:2,
						img:"../../static/images/5.png",
						name:"红茶"
					},
					{
						id:3,
						img:"../../static/images/11.png",
						name:"黑茶"
					},
					{
						id:4,
						img:"../../static/images/2.png",
						name:"普洱"
					},
					{
						id:5,
						img:"../../static/images/4.png",
						name:"普洱"
					},
					{
						id:6,
						img:"../../static/images/5.png",
						name:"普洱"
					}
				]
			},
			config:{
				more:true,
				autoplay:false,
				multiple:4,
				shadow:true
			},
			videosrc:'../../static/videos/f2.mp4',  
			poster:'../../static/images/swiper2 .png',//封面图片
			title:"视频",
			gDuration:60,
			first:true,
			second:false,
			list: [{
					title: '茶界'
				}, {
					title: '茶道'
				}
			],
			type: [{
				title: '茶界'
				}, {
				title: '茶道'
				}],
			active: 0
		}
	},
	onReady: function(res) {

			this.videoContext = uni.createVideoContext('myVideo')

	},
	methods: {
		videoErrorCallback: function(e) {
				uni.showModal({
						content: e.target.errMsg,
						showCancel: false
				})
		},
		handleChange(){},
		changeIndicatorDots(e) {
				this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
				this.autoplay = !this.autoplay
		},
		intervalChange(e) {
				this.interval = e.target.value
		},
		durationChange(e) {
				this.duration = e.target.value
		},
		swiperChange(current){
			console.log(current);
		},
		tapFun(e){
			console.log(e.type,e.id,e.index);
		}
	},
	watch:{
		active() {
			if(this.active=='0'){
				this.first=true;
				this.second=false;									
			}else{					
				this.first=false;	
				this.second=true;					
			}
		}
	}
}
